.features {
  display: grid;
  gap: 20px 30px;
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 1.2;
  text-align: left;
  color: var(--color-dark-gray);

  @media (min-width: 640px) {
    grid-template-columns: 1fr min-content min-content;
  }

  @media (min-width: $width-tablet) {
    column-gap: 48px;
    max-width: 686px;
    font-size: 1.8rem;
    line-height: 1.4;
  }
}

.features__item {
  position: relative;
  display: grid;
  grid-template-columns: 38px 1fr;
  align-items: center;
  gap: 10px;
  max-width: 320px;

  &::before {
    content: "";
    width: 38px;
    height: 38px;
    background: radial-gradient(circle at center, var(--color-blue) 6px, transparent 6px, transparent 13px, opacify(#322faf, 0.7) 13px, opacify(#322faf, 0.7) 14px, transparent 14px, transparent 18px, opacify(#322faf, 0.4) 18px);
    border-radius: 50%;
  }

  @media (min-width: 640px) {
    align-items: start;
  }
}
