.contact {
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 7px;
}

.contact__link {
  display: none;

  @media (min-width: $width-desktop) {
    display: block;
    font-size: 1.4rem;
    line-height: 1;
  }
}

.contact__phone {
  display: block;
  font-size: 1.8rem;
  line-height: 1;
  font-weight: 700;
}

.contact__time {
  display: block;
  font-size: 1.3rem;
  line-height: 1;
  color: var(--color-dark-gray);

  @media (min-width: $width-desktop) {
    font-size: 1.4rem;
  }
}
