.intro {
  padding-top: 12px;
  padding-bottom: 60px;
  text-align: center;

  @media (min-width: 640px) {
    padding-top: 35px;
    text-align: left;
  }

  @media (min-width: 768px) {
    padding-top: 70px;
  }

  @media (min-width: 1440px) {
    padding-top: 102px;
    padding-bottom: 120px;
  }
}

.intro__title {
  max-width: 1024px;
  margin: 0 auto 20px;

  @media (min-width: 640px) {
    max-width: 360px;
    margin: 0;
  }

  @media (min-width: 768px) {
    max-width: 480px;
  }

  @media (min-width: 1200px) {
    max-width: 590px;
  }
}

.intro__mark {
  margin: 0 auto 154px;

  @media (min-width: 640px) {
    margin: 25px 0 75px;
  }

  @media (min-width: 992px) {
    margin-top: -125px;
    margin-bottom: 155px;
    margin-left: 325px;
  }

  @media (min-width: 1200px) {
    margin-top: -175px;
    margin-bottom: 155px;
    margin-left: 432px;
  }
}

.intro__features {
  position: relative;
  display: flex;
  justify-content: center;
  margin-bottom: 25px;

  &::before {
    content: "";
    position: absolute;
    bottom: calc(100% - 60px);
    left: -15px;
    z-index: -1;
    width: calc(100% + 30px);
    height: 500px;
    background: url("../images/intro_bg-sm.jpg") 100% 100%/108.5% no-repeat;

    @media (min-width: 640px) {
      display: none;
    }
  }

  @media (min-width: 640px) {
    justify-content: start;
  }

  @media (min-width: 1200px) {
    margin-bottom: 90px;
  }
}

.warranty {
  display: inline-block;
  width: calc(100% + 30px);
  max-width: 375px;
  margin: -15px -15px -30px;

  @media (min-width: 640px) {
    float: right;
    max-width: 250px;
    margin-top: -420px;
  }

  @media (min-width: 1200px) {
    max-width: 300px;
  }
}

.intro__buttons {
  max-width: 590px;

  .button {
    width: 290px;

    @media (min-width: 640px) {
      width: 330px;
    }
  }

  @media (min-width: 640px) {
    flex-direction: row;
    align-items: center;
  }
}

