.calc {
  padding-bottom: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.calc__title {

  @media (min-width: 640px) {
    max-width: 700px;
    text-align: left;
    align-self: start;
  }

  @media (min-width: 1200px) {
    max-width: 885px;
  }
}

.calc__mark {
  margin: 20px auto 25px;

  @media (min-width: $width-tablet) {
    align-self: end;
    margin: 0;
    margin-top: 20px;
    margin-bottom: -65px;
  }

  @media (min-width: 1200px) {
    align-self: start;
    margin-left: 400px;
    margin-top: 15px;
  }
}

.calc__lead {
  max-width: 378px;
  margin: 0;
  margin-bottom: 25px;
  color: var(--color-dark-gray);

  @media (min-width: $width-tablet) {
    font-size: 1.8rem;
    text-align: left;
    align-self: start;
    margin-bottom: 60px;
  }
}

.calc__grid {
  position: relative;
  display: grid;
  justify-items: center;
  width: 100%;

  @media (min-width: 992px) {
    grid-template-columns: 60% 1fr;
  }

  @media (min-width: 1200px) {
    grid-template-columns: repeat(2, 1fr);
    gap: 0 110px;
  }
}

.calc__benefits {
  order: -1;
  display: grid;
  grid-template-columns: minmax(128px, 148px) minmax(134px, 258px);
  gap: 10px;
  margin-bottom: 25px;
  padding: 0 8px;

  @media (min-width: 992px) {
    order: 0;
    grid-area: 1 / 2 / 2 / 3;
    padding: 45px 0 0;
    margin-right: 25px;
  }

  @media (min-width: 1200px) {
    margin: 0;
    justify-self: start;
  }
}

.calc__benefits-item {
  margin: 0;
  min-height: 120px;
  padding: 18px 10px;
  font-size: 1.4rem;
  line-height: 1.3;
  text-align: center;
  color: var(--color-blue);
  background-color: white;
  border: 1px solid var(--color-blue);
  border-radius: 12px;

  @media (min-width: 1200px) {
    display: flex;
    align-items: center;
    text-align: left;
    padding: 20px;
  }
}

.calc__subtitle {
  grid-column: 1 / -1;
  margin: 0;
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #2A2C2B;

  @media (min-width: $width-tablet) {
    font-size: 1.6rem;
  }
}

.calc__form {
  width: calc(100% + 30px);
  margin: 0 -15px;
  padding: 35px 0 166px;
  background: url("../images/form_bg-sm.jpg") left 25% top / cover no-repeat;
  box-shadow: 0px 14px 54px rgba(0, 0, 0, 0.1);
  border-radius: 20px;

  @media (min-width: 640px) {
    width: 100%;
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
  }

  @media (min-width: 992px) {
    grid-area: 1 / 1 / 3 / 3;
    padding-top: 40px;
    padding-bottom: 115px;
  }

  @media (min-width: 1200px) {
    padding-left: 40px;
    padding-right: 40px;
    background-position: center;
  }
}

.calc__form-progress {
  max-width: 470px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $width-tablet) {
    margin-left: 0;
    margin-right: 0;
  }
}

.calc__count {
  display: inline-block;
  padding: 4px 9px;
  margin: 0 4px;
  background-color: #D0D6E4;
  border-radius: 4px;
}

.calc__author {
  position: absolute;
  left: 50%;
  bottom: 35px;
  transform: translateX(-50%);

  @media (min-width: 992px) {
    left: 0;
    bottom: 0;
    transform: translate(-25px, 10px);
  }
}
