.mark {
  display: flex;
  align-items: center;
  font-family: var(--font-secondary);
  font-size: 1.8rem;
  font-weight: 600;
  font-style: italic;
  color: var(--color-blue);
  background-repeat: no-repeat;
}

.mark--small {
  width: 145px;
  height: 39px;
  justify-content: center;
  background-image: url("../images/round-01.svg");
  background-position: center;
  background-size: 100% 100%;

  @media (min-width: 520px) {
    width: 190px;
    height: 50px;
    font-size: 2rem;
    background-image: url("../images/round-01-xl.svg");
  }

  @media (min-width: 992px) {
    width: 228px;
    height: 60px;
    font-size: 3.0rem;
  }
}

.mark--big {
  width: 290px;
  height: 84px;
  padding-left: 72px;
  text-align: left;
  background-image: url();
  background: url("../images/marker-warranty.svg") 30px 50% / 32px 36px no-repeat, url("../images/round-02.svg") center / 100% 100% no-repeat;

  @media (min-width: 992px) {
    width: 485px;
    height: 74px;
    padding-left: 65px;
    font-size: 2rem;
    background: url("../images/marker-warranty.svg") 35px 50% / 22px 28px no-repeat, url("../images/round-02-xl.svg") center / 100% 100% no-repeat;
  }
}
