.social {
  grid-template-columns: 72px 1fr;
  gap: 8px 20px;

  @media (min-width: $width-tablet) {
    width: 426px;
  }
}

.social__image {
  display: none;

  @media (min-width: $width-tablet) {
    grid-area: 1 / 1 / 3 / 2;
    align-self: center;
    justify-self: center;
    display: block;
    width: 60px;
    height: 60px;
    object-fit: cover;
    line-height: 1;
    color: var(--color-dark-gray);
    box-sizing: content-box;
    border-radius: 50%;
    border: 1px solid #E8E8E8;
    outline: 1px solid #E8E8E8;
    outline-offset: 6px;
  }
}

.social__text {
  display: none;

  @media (min-width: $width-tablet) {
    grid-area: 1 / 2 / 2 / 3;
    display: block;
    margin: 0;
    line-height: 1;
    color: var(--color-dark-gray);
  }
}

.social__list {
  display: grid;
  grid-auto-flow: column dense;
  align-items: center;
  height: 36px;
  margin: 0;
  padding: 0 3px;
  list-style: none;
  text-align: center;

  @media (min-width: $width-tablet) {
    width: 100%;
    grid-area: 2 / 2 / 3 / 3;
    padding: 0;
  }
}

.social__item {
  &:not(:last-of-type) {
    border-right: 1px solid #A0A0A0;

    @media (min-width: $width-tablet) {
      border: 0;
    }
  }
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  height: 100%;
  padding: 8px 12px;
  font-family: var(--font-secondary);
  font-size: 1.3rem;
  font-weight: 300;
  text-decoration: none;
  color: black;

  @media (min-width: $width-tablet) {
    gap: 6px;
    max-width: 334px;
    padding: 0;
    font-size: 1.4rem;
  }
}

.social__link--whatsapp {
  @media (min-width: $width-tablet) {
    padding: 8px 14px;
    background: #FFFFFF;
    border: 1px solid #C9C9C9;
    border-radius: 36px 0px 0px 36px;

    .social__icon {
      width: 20px;
      height: 20px;
      margin-left: 0;
    }
  }
}

.social__link--telegram {
  @media (min-width: $width-tablet) {
    padding: 8px 14px;
    background: #FFFFFF;
    border: 1px solid #C9C9C9;
    border-radius: 0px 36px 36px 0px;

    .social__icon {
      width: 20px;
      height: 20px;
      margin-left: 0;
    }
  }
}

.social__icon {
  @media (min-width: $width-tablet) {
    width: 28px;
    height: 28px;
    margin-left: 10px;
  }
}
