.header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 84px;
  padding-right: 35px;

  .social {
    display: none;

    @media (min-width: 992px) {
      display: grid;
      margin: 0 auto;
    }
  }

  @media (min-width: 1200px) {
    padding-left: 35px;
    height: 120px;
  }

  @media (min-width: $width-desktop) {
    padding-left: calc(50% - 600px);
    padding-right: calc(50% - 600px);
  }

  @media (min-width: 1680px) {
    padding-left: calc(50% - 720px);
    padding-right: calc(50% - 720px);
  }
}

.header--fixed {
  height: 60px;
  position: fixed;
  z-index: 100;
  background-color: white;
  box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.15);

  .social {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 100;
    display: block;
    width: 100%;
    background-color: white;
    box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.15);
  }
}

.header__logo {
  width: auto;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  border-top-left-radius: 0;

  @media (min-width: 1200px) {
    width: 90px;
    height: 90px;
    border-radius: 50%;
  }
}

.header__text {
  display: none;

  @media (min-width: $width-tablet) {
    display: block;
    max-width: 215px;
    margin-left: 25px;
    font-size: 1.4rem;
    color: var(--color-dark-gray);
  }
}

.header__contact {
  margin: 0 20px 0 auto;

  @media (min-width: 1200px) {
    margin-right: 30px;
  }
}
