.progress {
  position: relative;
  width: calc(100% - 30px);
  height: 12px;
  margin: 20px auto 0;
  border-radius: 44px;
  overflow: hidden;

  @media (min-width: 1200px) {
    width: 100%;
  }
}

.progress__back {
  width: 100%;
  height: 100%;
  background: #D0D6E4;
  opacity: 0.5;
}

.progress__current {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% / 2);
  height: 100%;
  background: #68CA37 repeating-linear-gradient(-45deg, transparent 0 11px, rgba(255, 255, 255, 0.2) 11px 22px);
  border-top-right-radius: 44px;
  border-bottom-right-radius: 44px;
}
