.form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 70px auto;
  max-width: 470px;
  min-height: 374px;
  gap: 30px 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;

  @media (min-width: $width-tablet) {
    margin-left: 0;
    margin-right: 0;
    padding: 0;
  }
}

.form__title {
  align-self: end;
  grid-column: 1 / 3;
  margin: 0;
  font-size: 2rem;
  line-height: 1.165;
  font-weight: 700;

  @media (min-width: $width-tablet) {
    font-size: 3rem;
    text-align: left;
  }
}

.form__card {
  display: grid;
  grid-template-rows: 1fr 35px;
  gap: 10px;
  min-height: 150px;
  padding: 12px;
  text-align: left;
  background: white;
  border: 1px solid #C9CACA;
  border-radius: 6px;

  @media (min-width: $width-tablet) {
    grid-template-rows: 1fr 45px;
    min-height: 218px;
  }
}

.form__image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  align-self: center;
}

.form__buttons {
  grid-column: 1 / 3;

  .button {
    text-align: center;
  }
}

input[type="radio"] {
  display: none;

  &:checked~label::before {
    background: #68CA37;
    opacity: 0.7;
  }

  &:checked~label::after {
    background: #68CA37 url("data:image/svg+xml,%3Csvg width='18' height='13' viewBox='0 0 18 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.818961 7.13623C0.691688 7.00896 0.628052 6.81805 0.628052 6.69078C0.628052 6.5635 0.691688 6.37259 0.818961 6.24532L1.70987 5.35441C1.96442 5.09987 2.34623 5.09987 2.60078 5.35441L2.66442 5.41805L6.16442 9.1726C6.29169 9.29987 6.4826 9.29987 6.60987 9.1726L15.1371 0.32714H15.2008C15.4553 0.0725941 15.8371 0.0725941 16.0917 0.32714L16.9826 1.21805C17.2371 1.47259 17.2371 1.85441 16.9826 2.10896L6.80078 12.6726C6.67351 12.7999 6.54623 12.8635 6.35532 12.8635C6.16442 12.8635 6.03714 12.7999 5.90987 12.6726L0.946234 7.32714L0.818961 7.13623Z' fill='white'/%3E%3C/svg%3E%0A") center / 17px 13px no-repeat;
    opacity: 1;
  }
}

label {
  position: relative;
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: center;
  gap: 0 9px;

  &::before,
  &::after {
    content: "";
    border-radius: 50%;
  }

  &::before {
    width: 35px;
    height: 35px;
    background: #ECEFF5;
    opacity: 0.95;

    @media (min-width: $width-tablet) {
      width: 45px;
      height: 45px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 4px;
    left: 4px;
    width: 27px;
    height: 27px;
    background: #D0D6E4 url("data:image/svg+xml,%3Csvg width='17' height='13' viewBox='0 0 17 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.618643 7.13623C0.491371 7.00896 0.427734 6.81805 0.427734 6.69078C0.427734 6.5635 0.491371 6.37259 0.618643 6.24532L1.50955 5.35441C1.7641 5.09987 2.14592 5.09987 2.40046 5.35441L2.4641 5.41805L5.9641 9.1726C6.09137 9.29987 6.28228 9.29987 6.40955 9.1726L14.9368 0.32714H15.0005V0.32714C15.255 0.0725941 15.6368 0.0725941 15.8914 0.32714L16.7823 1.21805C17.0368 1.47259 17.0368 1.85441 16.7823 2.10896L6.60046 12.6726C6.47319 12.7999 6.34592 12.8635 6.15501 12.8635C5.9641 12.8635 5.83683 12.7999 5.70955 12.6726L0.745916 7.32714L0.618643 7.13623V7.13623Z' fill='%23242426'/%3E%3C/svg%3E%0A") center / 17px 13px no-repeat;
    opacity: 0.4;

    @media (min-width: $width-tablet) {
      top: 5px;
      left: 5px;
      width: 35px;
      height: 35px;
    }
  }
}
