.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;

  .link {
    white-space: nowrap;
  }
}

.button {
  display: block;
  width: 100%;
  height: 70px;
  padding: 12px 34px;
  color: white;
  font-family: var(--font-secondary);
  font-size: 1.6rem;
  line-height: 1.25;
  font-weight: 600;
  text-align: left;
  background: linear-gradient(273.05deg, #5C9D3B 2.34%, #55AE28 84.43%);
  box-shadow: 0px 4px 0px #3E7821, 0px 24px 54px rgba(180, 236, 190, 0.3), inset 0px 4px 17px rgba(255, 255, 255, 0.25), inset 0px -6px 7px rgba(255, 255, 255, 0.25);
  border-radius: 66px;

  @media (min-width: $width-tablet) {
    font-size: 1.8rem;
    height: 80px;
  }
}

.button__text {
  max-width: 190px;

  @media (min-width: $width-tablet) {
    max-width: 220px;
  }
}
