.project {
  display: none;

  @media (min-width: $width-desktop) {
    position: relative;
    display: block;
    width: 160px;
    height: 120px;
    text-align: center;
    align-self: start;
  }
}

.project__image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.project__link {
  display: block;
  padding-top: 65px;
  font-size: 1.4rem;
  line-height: 1.2;
}
