.menu {}

.menu--open {
  .menu__list {
    position: absolute;
    top: 60px;
    right: 0;
    display: block;
    width: 200px;
    padding: 25px 20px;
    background-color: white;
    box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.15);
  }
}

.toggle {
  display: block;
  width: 40px;
  height: 24px;
  padding: 0;
  background: transparent url("data:image/svg+xml,%3Csvg width='41' height='25' viewBox='0 0 41 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='2.74487' y1='2.38501' x2='38.7449' y2='2.38501' stroke='black' stroke-width='4' stroke-linecap='round'/%3E%3Cline x1='2.74487' y1='12.385' x2='18.7449' y2='12.385' stroke='black' stroke-width='4' stroke-linecap='round'/%3E%3Cline x1='2.74487' y1='22.385' x2='30.8024' y2='22.385' stroke='black' stroke-width='4' stroke-linecap='round'/%3E%3C/svg%3E%0A") center/cover no-repeat;
  border: none;

  @media (hover: hover) {
    &:hover {
      opacity: 0.5;
    }
  }
}

.menu__list {
  display: none;
}

