.container {
  padding-left: 15px;
  padding-right: 15px;

  @media (min-width: 640px) {
    padding-left: 30px;
    padding-right: 30px;
  }

  @media (min-width: 992px) {
    padding-left: 50px;
    padding-right: 50px;
  }

  @media (min-width: $width-desktop) {
    padding-left: calc(50% - 600px);
    padding-right: calc(50% - 600px);
  }
}
